import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Space, Table, Button, Input, message, DatePicker, List, Comment, Avatar, Modal } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import http from '@/service'
import moment from 'moment'

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default (props) => {


  const [info, setInfo] = useState({})
  const [signList, setSignList] = useState([])
  const [act, setAct] = useState('')
  const [currBuyBook, setCurrBuyBook] = useState({})
  const [pj, setPj] = useState('')
  const navigate = useNavigate() 
  const handleBuy = (record) => {
    setAct('pj')
    setCurrBuyBook(record)

  }
  const getSignList = () => {
    http({
      url: '/api/signList',
      data: { orderId: currBuyBook.orderId, pj },
      method: 'POST'
    }).then((res) => {
      setSignList(res.value)
    })

  }

  const handleDel = (r) => {
    Modal.confirm({
      title: '请确认操作',
      onOk: () => {
        http({
          url: '/api/delSign',
          data: { _id: r._id },
          method: 'POST'
        }).then((res) => {
          message.success('删除成功')
          getSignList()
        })
      }
    })
  }

  const columns = [
    {
      title: '使用密钥',
      dataIndex: 'key',
      key: 'key',
      render: (v) => {
        return v[0]?.title
      }
    },
    {
      title: '签名状态',
      dataIndex: 'book',
      key: 'detail',
      render: () => {
        return '成功'
      }

    },
    {
      title: '文件下载',
      dataIndex: 'file',
      key: 'file',
      render: (v) => {
        return <a href={v?.fileList[0]?.response.filePathName}>{v?.fileList[0]?.name}</a>
      }

    },
    {
      title: '已签名文件下载',
      dataIndex: 'jarSignFile',
      key: 'jarSignFile',
      render: (v, r) => {
        return <a href={v}>{r.file?.fileList[0]?.name.replace('.apk', '_signed.apk')}</a>
        // return <a href={v}>{v && v.replace(/.+\//, '')}</a>
      }

    },

    {
      title: '操作',
      key: 'action',
      render: (text, record) => (
        <Space size="middle">
          <Button onClick={() => handleDel(record)}>删除</Button>
        </Space>
      ),
    },
  ];


  useEffect(() => {
    http({
      url: '/api/getInfo'
    }).then((res) => {
      res.value && setInfo(res.value)

    })

    getSignList()
  }, [])
  return <div>
        <Table pagination={{pageSize:5}} columns={columns} dataSource={signList.filter(props.filter ? props.filter: (e) => info.isAdmin || info.username === e.username)} />

  </div>
}