import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Breadcrumb, Button, Form, Input, message, Select, Upload } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import SignList from '../signList/index'
import http from '@/service'

/**
 * Auther lizheyu
 * nav1234表示区域 改掉即可
 * 图片保存在public目录下 名称就是nav1/nav2 比如你区域是大厅 那图片就是大厅.jpeg
 * 
 */

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default () => {
  const [info, setInfo] = useState({})
  const [key, setKey] = useState([])
  const [loading, setLoading] = useState(false)
  const fromRef = useRef({})
  const onFinish = (values) => {
    console.log('Success:', values);
    setLoading(true)
    http({
      url: '/api/sign',
      data: { ...values },
      method: 'POST'
    }).then((res) => {
      if (res.code === 0) {
        message.success('签名成功')
      } 
      setLoading(false)
    })
  };

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };


  const getKeyList = () => {
    http({
      url: '/api/myKeyList',
      method: 'POST'
    }).then((res) => {
      res.value && setKey(res.value.filter((e) => !e.isFrozen))

    })
  }
  useEffect(() => {
    getKeyList()
    http({
      url: '/api/getInfo'
    }).then((res) => {
      res.value && setInfo(res.value)
      fromRef.current.setFieldsValue(res.value)

    })
  }, [])
  return <div>
    <Form
      name="basic"
      labelCol={{ span: 4 }}
      wrapperCol={{ span: 16 }}
      initialValues={{ remember: true }}
      onFinish={onFinish}
      onFinishFailed={onFinishFailed}
      autoComplete="off"
      ref={(e) => { fromRef.current = e }}
    >
      <Form.Item
        label="选择密钥"
        name="keyId"
        rules={[{ required: true }]}
      >
        <Select options={key.filter((e) => e.isCheck).map((item) => ({value: item.id, label: item.title}))} />
      </Form.Item>

      <Form.Item
        label="上传文件"
        name="file"
        rules={[{ required: true }]}
      >
        <Upload
          action='/common/upload'
          headers={{
            authorization: 'authorization-text',
          }}
          maxCount={1}
          onChange={(info) => {
            console.log(info)
            if (info.file.status !== 'uploading') {
              console.log(info.file, info.fileList);
            }
            if (info.file.status === 'done') {
              message.success(`${info.file.name} file uploaded successfully`);
            } else if (info.file.status === 'error') {
              message.error(`${info.file.name} file upload failed.`);
            }
          }}
        >
          <Button >Click to Upload</Button>
        </Upload>
      </Form.Item>


      <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
        <Button type="primary" htmlType="submit" loading={loading}>
          签名
        </Button>
      </Form.Item>
    </Form>
    <SignList></SignList>



  </div>
}