import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Breadcrumb, Button, Form, Input, message, Space, Table, Upload, Radio, DatePicker, Row, Col, Modal, Progress } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import http from '@/service'
import moment from 'moment'
import { useNavigate } from 'react-router-dom'
import './index.scss'
const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 14 },
};

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default (props) => {
  const [info, setInfo] = useState({})
  const [act, setAct] = useState('')
  const [currKey, setCurrKey] = useState('')
  const [key, setKey] = useState([])
  const zidanRef = useRef(null)
  const searchFromRef = useRef({})
  const navigate = useNavigate()
  const [renPos, setRenPos] = useState({ x: 0, y: 0 })
  const [boxPos, setBoxPos] = useState({ x: 0, y: 0 })
  const [xie, setXie] = useState(100)
  const xieRef = useRef(100)
  const [isShowModal, setIsShowModal] = useState(false)
  const renPosRef = useRef({ x: 0, y: 0 })
  const boxPosRef = useRef({ x: 0, y: 0 })
  const [timuNum, setTimuNum] = useState(0)
  const timersRef = useRef([])
  
  const promiseModal = (obj) => new Promise((resolve) => {
    Modal.success({
      title: '提示',
      ...obj,
      onOk: (...args) => {
        resolve()
        obj.onOk && obj.onOk(...args)
      }
    })
  })


  const onFinish = (e) => {
    console.log(e)
    if (props.timu[timuNum].answer === e[props.timu[timuNum].title]) {
      console.log('正确')
      message.success('回答正确 加强攻击')
      initBox()
      if (timuNum + 1 > props.timu.length -1) {
        props.next()
        timersRef.current.forEach((t) => {
          clearTimeout(t)
        })
        return
      }
      setTimuNum(timuNum + 1)
      setIsShowModal(false)
      createZidan()
      // props.next()
    } else {
      console.log('错误')
      setIsShowModal(false)
      setTimuNum(0)
      props.reset()

    }
    
  }

  const runTimu = () => {
    setIsShowModal(true)
  }

  const startGuaiwuMove = () => {
    const guaiwu = document.querySelector('.game-guaiwu-guaiwu')
    const timer = setInterval(() => {
      guaiwu.style.right = Number.parseInt(guaiwu.style.right || 0) + 3 + 'px'
      if (guaiwu.offsetLeft ===0) {
        clearInterval(timer)
        return
      }
      if (guaiwu.offsetLeft < 80) {
        props.reset()
        clearInterval(timer)
        //输了
      }
    }, 1000);
    // timersRef.current.push(timer)
    // setTimuNum(timersRef.current.length)
  }

  // const createGongji = () => {
  //   const timer = setInterval(() => {
  //     // createZidan(() => clearInterval(timer))
  //   }, 1000);
  // }

  const createZidan = (callback) => {
    const box = document.querySelector('.game-guaiwu')
    const guaiwu = document.querySelector('.game-guaiwu-guaiwu')
    const zidan = document.createElement('img')
    box.appendChild(zidan)

    zidan.src = '/img/zidan.png'
    zidan.className = 'game-guaiwu-zidan'
    zidan.style.height = '10px'
    zidan.style.width = '10px'
    zidan.style.position = 'absolute'
    zidan.style.left = '80px'
    zidan.style.top = '40px'
    zidan.style.borderRadius = '5px'


    const timer = setInterval(() => {
    zidan.style.left = Number.parseInt(zidan.style.left) + 10 + 'px'
    if (guaiwu.offsetLeft - zidan.offsetLeft < 0) {
      console.log('打到了')
      const currXie = xieRef.current -1
      box.removeChild(zidan)
      if (currXie <=0) {
        props.next()
        timersRef.current.forEach((t) => {
          clearTimeout(t)
        })
      }
      setXie(currXie)
      xieRef.current = currXie
      clearInterval(timer)
      if (guaiwu.offsetLeft > 80) {
        const timer = setTimeout(() => {
          createZidan()
          
        }, 8000);
        timersRef.current.push(timer)
        // callback && callback()
      }
    }
    }, 20);
    timersRef.current.push(timer)
  }

  useEffect(() => {
    const run = async () => {
      await promiseModal({
        title: '提示',
        content: '博士欣慰的看了你一眼，让你去城头等他。你来到了城墙边上，离奇的是城外阴云密布，城内却万里无云，不过城墙上守城将士的严肃姿态还是让你察觉到这次危机的非同寻常'
      })
      await promiseModal({
        title: '提示',
        content: '这时博士找到你，他需要你的帮助，你和他一起来到了城墙上大炮的边上，博士告诉你接下来将由你来操控大炮进行防御，这是系统也触发了任务：临危受命。你望着身后这座你刚来不久的城市，选择接受了这个任务。操控大炮击退病毒'
      })

    }
    run()
  }, [])

  useEffect(() => {
    createZidan()
    startGuaiwuMove()

  }, [])

  useEffect(() => {
    http({
      url: '/api/getInfo'
    }).then((res) => {
      res.value && setInfo(res.value)
    })

  }, [])
  useEffect(() => {
    if (props.key1=== 0) return
    setXie(100)
    xieRef.current = 100
    const guaiwu = document.querySelector('.game-guaiwu-guaiwu')
    guaiwu.style.right = '0px'
    timersRef.current.forEach((t) => {
      clearTimeout(t)
    })
    Array.from(document.querySelectorAll('.game-guaiwu-zidan')).forEach((img) => {
      console.log(img)
      img.parentNode.removeChild(img)
    })

    createZidan()

  }, [props.key1])

  const initBox = () => {
    const ele = document.querySelector('.game-xiangshuren-background')
    const eleBox = document.querySelector('.game-xiangshuren-box')
    const maxY = ele.clientHeight - eleBox.clientHeight
    const maxX = ele.clientWidth - eleBox.clientWidth
    const pos = {
      x: Number.parseInt(maxX * Math.random()),
      y: Number.parseInt(maxY * Math.random())
    }
    setBoxPos(pos)
    boxPosRef.current = pos
  }

  useEffect(() => {
    const ele = document.querySelector('.game-xiangshuren-background')
    const eleBox = document.querySelector('.game-xiangshuren-box')
    ele.addEventListener('load', () => {
      initBox()
    }, false)

    window.addEventListener('keyup', (e) => {
      const ele = document.querySelector('.game-xiangshuren-background')
      const eleRen = document.querySelector('.game-xiangshuren-ren')
      const maxY = ele.clientHeight - eleRen.clientHeight
      const maxX = ele.clientWidth - eleRen.clientWidth
      let pos = renPosRef.current
      // console.log(e)
      switch (e.code) {
        case 'ArrowUp':
          pos = { ...renPosRef.current, y: renPosRef.current.y - 20 }

          break
        case 'ArrowDown':
          pos = { ...renPosRef.current, y: renPosRef.current.y + 20 }

          break
        case 'ArrowLeft':
          pos = { ...renPosRef.current, x: renPosRef.current.x - 20 }

          break
        case 'ArrowRight':
          pos = { ...renPosRef.current, x: renPosRef.current.x + 20 }

          break
        case 'Space':
          const jvliX = Math.abs(renPosRef.current.x - boxPosRef.current.x)
          const jvliY = Math.abs(renPosRef.current.y - boxPosRef.current.y)
          console.log(jvliX, jvliY)
          if (jvliX < 25 && jvliY < 25) {
            runTimu()

            console.log('捡到了')
          } else {
            message.warning('请靠近道具拾取')
          }


          break
      }

      if (pos.y > maxY) return
      if (pos.x > maxX) return
      if (pos.y < 0) return
      if (pos.x < 0) return
      setRenPos(pos)
      renPosRef.current = pos
    }, false)
  }, [])
  return <div>
    <p>按键盘上下左右键移动小人，按空格键拾取</p>
    <div className='game-xiangshuren'>
      <img className='game-xiangshuren-background' src="/img/bj4.png" alt="" />
      <img style={{
        left: renPos.x,
        top: renPos.y,

      }} className='game-xiangshuren-ren' src="/img/xiangshuren.png" alt="" />
      <img style={{
        left: boxPos.x,
        top: boxPos.y,

      }} className='game-xiangshuren-box' src="/img/baoxiang.png" alt="" />


    </div>
    <p>怪物血条</p>
    <Progress percent={xie} status="active" strokeColor="red" />
    <div className='game-guaiwu'>
      <img className='game-guaiwu-guaiwu' src="/img/guaiwut.png" alt="" />
      <img className='game-guaiwu-paotai' src="/img/paotait.png" alt="" />
    </div>

    <Modal title="答题" visible={isShowModal} footer={false} closable={false}>
      <Form
        name="validate_other"
        {...formItemLayout}
        onFinish={onFinish}
        initialValues={{
          'input-number': 3,
          'checkbox-group': ['A', 'B'],
          rate: 3.5,
        }}
      >
        <Form.Item name={props.timu[timuNum].title} label={props.timu[timuNum].title}>
            <Radio.Group>
            {props.timu[timuNum].dataSource.map((item2) => {
              return <Radio value={item2}>{item2}</Radio>
            })}
              
            </Radio.Group>
            
          </Form.Item>
          
        
              <Form.Item wrapperCol={{ span: 12, offset: 6 }}>
        <Button type="primary" htmlType="submit">
          Submit
        </Button>
      </Form.Item>
      </Form>
    </Modal>

  </div>
}