import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Breadcrumb, Button, Form, Input, message, Space, Table, Upload, Radio, DatePicker, Row, Col, Modal } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import http from '@/service'
import moment from 'moment'
import { useNavigate } from 'react-router-dom'
import './index.scss'
const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 14 },
};

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;

const promiseModal = (obj) => new Promise((resolve) => {
  Modal.success({
    title: '提示',
    ...obj,
    onOk: (...args) => {
      resolve()
      obj.onOk && obj.onOk(...args)
    }
  })
})

export default (props) => {
  const [info, setInfo] = useState({})
  const [act, setAct] = useState('')
  const [currKey, setCurrKey] = useState('')
  const [key, setKey] = useState([])
  const fromRef = useRef({})
  const searchFromRef = useRef({})
  const navigate = useNavigate()
  const [renPos, setRenPos] = useState({ x: 0, y: 0 })
  const [boxPos, setBoxPos] = useState({ x: 0, y: 0 })
  const [isShowModal, setIsShowModal] = useState(false)
  const renPosRef = useRef({ x: 0, y: 0 })
  const boxPosRef = useRef({ x: 0, y: 0 })

  const getKeyList = (data) => {
    http({
      url: '/api/keyList',
      method: 'POST',
      data
    }).then((res) => {
      res.value && setKey(res.value)

    })
  }

  const onFinish = (e) => {
    console.log(e)
    if (props.timu.every((item) => item.answer === e[item.title])) {
      console.log('正确')
      props.next()
    } else {
      console.log('错误')
      props.reset()


    }
    
  }

  const runTimu = () => {
    setIsShowModal(true)
  }

  useEffect(() => {
    const run = async () => {
      await promiseModal({
        title: '提示',
        content: '你继续在街上逛着，突然一处人声鼎沸的店铺吸引了你的注意，你凑过去发现那是一家名叫解密小屋的小店，他们正在举办活动，参加可以获得一份具有唯一性的“藏宝图”，你看了一会决定离开，这时候系统突然发布任务请在一分钟内参与该活动，任务奖励：未知。来不及多想的你决定接下任务。说来也巧这时上一个人刚刚结束挑战，你赶紧上前。开始后你发现是玩游戏。'
      })
      await promiseModal({
        title: '提示',
        content: '马里奥闯关，第一关限时3分钟，第二关2.5分钟，第三关2分钟。每关三个金币，两个炸弹，一个果子，吃到金币加速，吃到炸弹减速并弹出10秒的知识弹窗，吃到果子后完成关卡，获得密文（5859），第二关获得秘钥（n=11413，e=7467），第三关得到加密类型（RSA算法）。拿到“藏宝图”然后根据这些线索推导出原文（1415），在藏宝图坐标（14,15）找到了宝藏'
      })
      await promiseModal({
        title: '提示',
        content: '获得宝藏欣喜不已的你突然发现你已经来到了城墙下，雄伟的城墙犹如一条巨龙盘旋守护着这座城市，你心中升腾起无限的敬仰，这时突然有个老人向你走来，你发现了自己的失态连忙告罪，他却说他也曾和你一样说着说起了他的往事，你静静的听着偶尔发问。老人：这城墙可有点历史了，人们送了他一个名字叫做防火墙。第一代防火墙采用了包过滤（Packet filter）技术。后面人们将他升级成了电路层防火墙，接着又有了应用层防火墙（代理防火墙）的初步结构，到了1992年，人们基于动态包过滤（Dynamic packet filter）技术重新加固了它，到了今天第五代防火墙已经运用了自适应代理（Adaptive proxy）技术 。我：真是漫长的发展史，倾注了几代人的心血。老人：它自建成以来就一直屹立于此，位于被保护网络和外部网络之间执行访问控制策略的一个或一组系统，包括硬件和软件，构成一道屏障，以防止发生对被保护网络的不可预测的、潜在破坏性的侵扰。你：我知道，它过滤进、出网络的数据；管理进、出网络的访问行为； 封堵某些禁止的业务； 记录通过防火墙的信息内容和活动'
      })
      await promiseModal({
        title: '提示',
        content: '并对网络攻击进行检测和告警。畅谈了许久，老人告辞离开了，而你久久不能平息自己的心，系统提示：触景生情，你对防火墙技术有所领悟。滴，触发任务：呼唤，领悟了防火墙技术的你感受到了城中某种东西的呼唤。你笑了笑，注视了一会这饱经风霜的墙，慢慢向城中走去。'
      })
      await promiseModal({
        title: '提示',
        content: '在系统的指引下你来到了城中的入侵检测管理局，这时一位博士向你走来，这是系统为你安排的今天的导游。博士：尊贵的客人请跟我来。你跟上了博士。一路上博士给你讲解了许多与入侵检测相关的理论和技术，结合防火墙技术你颇有所得。这时博士突然提出要送你一份礼物，但是你需要接受他的考验。你自然从善如流，接受了这份考验'
      })

    }
    run()
  }, [])

  useEffect(() => {
    http({
      url: '/api/getInfo'
    }).then((res) => {
      res.value && setInfo(res.value)
    })

  }, [])

  useEffect(() => {
    const ele = document.querySelector('.game-xiangshuren-background')
    const eleBox = document.querySelector('.game-xiangshuren-box')
    ele.addEventListener('load', () => {
      const maxY = ele.clientHeight - eleBox.clientHeight
      const maxX = ele.clientWidth - eleBox.clientWidth
      const pos = {
        x: Number.parseInt(maxX * Math.random()),
        y: Number.parseInt(maxY * Math.random())
      }
      setBoxPos(pos)
      boxPosRef.current = pos
    }, false)

    window.addEventListener('keyup', (e) => {
      const ele = document.querySelector('.game-xiangshuren-background')
      const eleRen = document.querySelector('.game-xiangshuren-ren')
      const maxY = ele.clientHeight - eleRen.clientHeight
      const maxX = ele.clientWidth - eleRen.clientWidth
      let pos = renPosRef.current
      // console.log(e)
      switch (e.code) {
        case 'ArrowUp':
          pos = { ...renPosRef.current, y: renPosRef.current.y - 20 }

          break
        case 'ArrowDown':
          pos = { ...renPosRef.current, y: renPosRef.current.y + 20 }

          break
        case 'ArrowLeft':
          pos = { ...renPosRef.current, x: renPosRef.current.x - 20 }

          break
        case 'ArrowRight':
          pos = { ...renPosRef.current, x: renPosRef.current.x + 20 }

          break
        case 'Space':
          const jvliX = Math.abs(renPosRef.current.x - boxPosRef.current.x)
          const jvliY = Math.abs(renPosRef.current.y - boxPosRef.current.y)
          console.log(jvliX, jvliY)
          if (jvliX < 25 && jvliY < 25) {
          runTimu()

            console.log('捡到了')
          } else {
            message.warning('请靠近宝箱拾取')

          }

          break
      }

      if (pos.y > maxY) return
      if (pos.x > maxX) return
      if (pos.y < 0) return
      if (pos.x < 0) return
      setRenPos(pos)
      renPosRef.current = pos
    }, false)
  }, [])
  return <div>
    <p>按键盘上下左右键移动小人，按空格键拾取</p>

    <div className='game-xiangshuren'>
    <img className='game-xiangshuren-background' src="/img/bj2.png" alt="" />

      <img style={{
        left: renPos.x,
        top: renPos.y,

      }} className='game-xiangshuren-ren' src="/img/xiangshuren.png" alt="" />
      <img style={{
        left: boxPos.x,
        top: boxPos.y,

      }} className='game-xiangshuren-box' src="/img/baoxiang.png" alt="" />


    </div>
    <Modal title="答题" visible={isShowModal} footer={false} onCancel={props.reset}>
      <Form
        layout="vertical"
        name="validate_other"
        // {...formItemLayout}
        onFinish={onFinish}
        initialValues={{
          'input-number': 3,
          'checkbox-group': ['A', 'B'],
          rate: 3.5,
        }}
      >
        {props.timu.map((item) => {
          return <Form.Item name={item.title} label={item.title}>
            <Radio.Group>
            {item.dataSource.map((item2) => {
              return <Radio value={item2}>{item2}</Radio>
            })}
              
            </Radio.Group>
            
          </Form.Item>
          
        })}
              <Form.Item wrapperCol={{ span: 12, offset: 6 }}>
        <Button type="primary" htmlType="submit">
          Submit
        </Button>
      </Form.Item>
      </Form>
    </Modal>

  </div>
}