import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Breadcrumb, Button, Form, Input, message, Space, Table, Upload, Radio, DatePicker, Row, Col, Modal } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import http from '@/service'
import moment from 'moment'
import {useNavigate} from 'react-router-dom'

/**
 * Auther lizheyu
 * nav1234表示区域 改掉即可
 * 图片保存在public目录下 名称就是nav1/nav2 比如你区域是大厅 那图片就是大厅.jpeg
 * 
 */

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default () => {
  const [info, setInfo] = useState({})
  const [act, setAct] = useState('')
  const [currKey, setCurrKey] = useState('')
  const [key, setKey] = useState([])
  const fromRef = useRef({})
  const searchFromRef = useRef({})
  const navigate = useNavigate()
  const handleDel = (record) => {
    Modal.confirm({
      title: '请确认操作',
      onOk: () => {
        http({
          url: '/api/delKey',
          method: 'POST',
          data: {
            id: record.id
          }
        }).then((res) => {
          getKeyList()
        })
      }
    })
  }

  const handleCheck = (record) => {
    http({
      url: '/api/addKey',
      data: { ...record, isCheck: true },
      method: 'POST'
    }).then(() => {
      getKeyList()
    })
  }
  const handleEdit = (record) => {
    setAct('edit')
    setCurrKey(record.id)
    setTimeout(() => {
      fromRef.current.setFieldsValue({ ...record })
    }, 10);
  }

  const handleFrozen = (record) => {
    http({
      url: '/api/addKey',
      data: { ...record, isFrozen: !record.isFrozen },
      method: 'POST'
    }).then(() => {
      getKeyList()
    })
  }

  const handleGetAuth = (record) => {
    http({
      url: '/api/getKeyAuth',
      method: 'POST',
      data: {
        keyId: record.id,
      }
    }).then((res) => {
      message.success('申请成功')
    })
  }

  const toDetail = (record) => {
    navigate('/keyDetail?keyId=' + record.id)
  }

  const columns = [
    {
      title: '名称',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: '用户名',
      dataIndex: 'username',
      key: 'username',
    },
    {
      title: 'ID',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: '密钥类型',
      dataIndex: 'envType',
      key: 'envType',
    },
    {
      title: '状态',
      dataIndex: 'isFrozen',
      key: 'isFrozen',
      render: (v,r) => {
        return r.isCheck ? (v ? '已冻结' : '未冻结') : '待审核'
      }
    },
    {
      title: '创建时间',
      dataIndex: 'time',
      key: 'time',
      render: (v) => {
        return moment(v).format('YYYY-MM-DD HH:mm:ss')
      }
    },

    {
      title: '操作',
      key: 'action',
      render: (text, record) => (
        <Space size="middle">
          {(info.isAdmin || record.username === info.username || record.users?.includes(info.username)) ?
            <React.Fragment>
              {(info.isAdmin && !record.isCheck) && <Button onClick={() => handleCheck(record)}>审核</Button>}

              <Button onClick={() => handleFrozen(record)}>{record.isFrozen ? '解冻' : '冻结'}</Button>
              <Button onClick={() => handleEdit(record)}>编辑</Button>
              <Button onClick={() => toDetail(record)}>查看详情</Button>
              {(info.isAdmin || record.username === info.username) && <Button onClick={() => handleDel(record)}>删除</Button>}
            </React.Fragment> :
            <React.Fragment>
              <Button onClick={() => handleGetAuth(record)}>申请</Button>

            </React.Fragment>
            

          }

        </Space>
      ),
    },
  ];

  const onFinish = (values) => {
    console.log('Success:', values);
    http({
      url: '/api/addKey',
      data: { ...values, id: currKey || undefined },
      method: 'POST'
    }).then((res) => {
      if (res.code === 0) {
        fromRef.current.setFieldsValue({})
        setAct('')
        setCurrKey('')
        getKeyList()
        message.success('操作成功')
      }
    })
  };

  const onSearch = (values) => {
    console.log(values)
    getKeyList({
      ...values,
      startTime: values.startTime?.valueOf(),
      endTime: values.endTime?.valueOf()
    })
  }

  const handleClearSearch = () => {
    getKeyList()
    searchFromRef.current.resetFields()
  }

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };

  const getKeyList = (data) => {
    http({
      url: '/api/keyList',
      method: 'POST',
      data
    }).then((res) => {
      res.value && setKey(res.value)

    })
  }

  useEffect(() => {
    http({
      url: '/api/getInfo'
    }).then((res) => {
      res.value && setInfo(res.value)

    })
    getKeyList()
  }, [])
  return <div>

    <Form
      name="basic"
      labelCol={{ span: 8 }}
      wrapperCol={{ span: 16 }}
      initialValues={{ remember: true }}
      onFinish={onSearch}
      onFinishFailed={onFinishFailed}
      autoComplete="off"
      ref={(e) => { searchFromRef.current = e }}
    >
            <Row>
        <Col span={12}>
      <Form.Item
        label="名称"
        name="title"
      >
        <Input />
      </Form.Item>
      <Form.Item
        label="ID"
        name="id"
      >
        <Input />
      </Form.Item>
      </Col>
      </Row>
      <Row>
        <Col span={12}>
      <Form.Item
        label="开始时间"
        name="startTime"
      >
        <DatePicker style={{width: '100%'}} />
      </Form.Item>
      </Col>
      <Col span={12}>
      <Form.Item
        label="结束时间"
        name="endTime"
      >
        <DatePicker style={{width: '100%'}} />
      </Form.Item>
      </Col>
      </Row>
      <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
        <Button type="primary" htmlType="submit">
          搜索
        </Button>
        <Button onClick={handleClearSearch} style={{marginLeft: 12}}>
          清空
        </Button>
      </Form.Item>
    </Form>

    {/* <Button type='primary' style={{marginBottom: 12}} onClick={() => {
      setAct('add')
    }}> 新增</Button> */}
    <Table columns={columns} dataSource={key} />

    {(act === 'edit' || act === 'add') && <Modal
    visible
    footer={false}
    title={act === 'edit' ? '编辑' : '新增'}
    onCancel={() => setAct('')}
  ><Form
      name="basic"
      labelCol={{ span: 4 }}
      wrapperCol={{ span: 16 }}
      initialValues={{ remember: true }}
      onFinish={onFinish}
      onFinishFailed={onFinishFailed}
      autoComplete="off"
      ref={(e) => { fromRef.current = e }}
    >
      <Form.Item
        label="名称"
        name="title"
      >
        <Input />
      </Form.Item>
      {info.isAdmin && <Form.Item
        label="密钥类型"
        name="envType"
      >
        <Radio.Group options={[{ label: '正式', value: '正式' }, { label: '测试', value: '测试' }]} />
      </Form.Item>}
      <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
        <Button type="primary" htmlType="submit">
          提交
        </Button>
      </Form.Item>
    </Form></Modal>}



  </div>
}