import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Breadcrumb, Button, Form, Input, message, Space, Table, Upload, Radio, DatePicker, Row, Col, Modal, Select } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import http from '@/service'
import GetAuthList from '../getAuthList'
import moment from 'moment'
import {useNavigate} from 'react-router-dom'

/**
 * Auther lizheyu
 * nav1234表示区域 改掉即可
 * 图片保存在public目录下 名称就是nav1/nav2 比如你区域是大厅 那图片就是大厅.jpeg
 * 
 */

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default () => {
  const [info, setInfo] = useState({})
  const [act, setAct] = useState('')
  const [currKey, setCurrKey] = useState('')
  const [key, setKey] = useState([])
  const fromRef = useRef({})
  const searchFromRef = useRef({})
  const navigate = useNavigate()
  const handleDel = (record) => {
    Modal.confirm({
      title: '请确认操作',
      onOk: () => {
        http({
          url: '/api/delKey',
          method: 'POST',
          data: {
            id: record.id
          }
        }).then((res) => {
          getKeyList()
        })
      }
    })
  }

  const handleEdit = (record) => {
    setAct('edit')
    setCurrKey(record.id)
    setTimeout(() => {
      fromRef.current.setFieldsValue({ ...record })
    }, 10);
  }

  const handleFrozen = (record) => {
    http({
      url: '/api/addKey',
      data: { ...record, isFrozen: !record.isFrozen },
      method: 'POST'
    }).then(() => {
      getKeyList()
    })
  }

  const handleGetAuth = (record) => {
    http({
      url: '/api/getKeyAuth',
      method: 'POST',
      data: {
        keyId: record.id,
      }
    }).then((res) => {
      message.success('申请成功')
      setTimeout(() => {
        window.location.reload()
      }, 500);

    })
  }

  const toDetail = (record) => {
    navigate('/keyDetail?keyId=' + record.id)
  }

  const columns = [
    {
      title: '名称',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: '用户名',
      dataIndex: 'username',
      key: 'username',
    },
    {
      title: 'ID',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: '密钥类型',
      dataIndex: 'envType',
      key: 'envType',
    },
    {
      title: '状态',
      dataIndex: 'isFrozen',
      key: 'isFrozen',
      render: (v,r) => {
        return r.isCheck ? (v ? '已冻结' : '未冻结') : '待审核'
      }
    },
    {
      title: '创建时间',
      dataIndex: 'time',
      key: 'time',
      render: (v) => {
        return moment(v).format('YYYY-MM-DD HH:mm:ss')
      }
    },

    {
      title: '操作',
      key: 'action',
      render: (text, record) => (
        <Space size="middle">
          {(info.isAdmin || record.username === info.username || record.users?.includes(info.username)) ?
            <React.Fragment>
              <Button onClick={() => handleFrozen(record)}>{record.isFrozen ? '解冻' : '冻结'}</Button>
              <Button onClick={() => handleEdit(record)}>编辑</Button>
              {(info.isAdmin || record.username === info.username) && <Button onClick={() => handleDel(record)}>删除</Button>}
            </React.Fragment> :
            <React.Fragment>
              <Button onClick={() => handleGetAuth(record)}>申请</Button>

            </React.Fragment>
            

          }
              <Button onClick={() => toDetail(record)}>查看详情</Button>

        </Space>
      ),
    },
  ];

  const onFinish = (values) => {
    console.log('Success:', values);
    http({
      url: '/api/addKey',
      data: { ...values, id: currKey || undefined },
      method: 'POST'
    }).then((res) => {
      if (res.code === 0) {
        fromRef.current.setFieldsValue({})
        setAct('')
        setCurrKey('')
        getKeyList()
        message.success('操作成功')
      }
    })
  };

  const onSearch = (values) => {
    console.log(values)
    getKeyList({
      ...values,
      startTime: values.startTime?.valueOf(),
      endTime: values.endTime?.valueOf()
    })
  }

  const handleClearSearch = () => {
    getKeyList()
    searchFromRef.current.resetFields()
  }

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };

  const getKeyList = (data) => {
    http({
      url: '/api/keyList',
      method: 'POST',
      data
    }).then((res) => {
      res.value && setKey(res.value)

    })
  }

  useEffect(() => {
    http({
      url: '/api/getInfo'
    }).then((res) => {
      res.value && setInfo(res.value)

    })
    getKeyList()
  }, [])
  const handleSq = (v) => {
    handleGetAuth({
      id: v.title
    })
  }

  return <div>

    


    {/* <Table columns={columns} dataSource={key} /> */}
    <GetAuthList btnRender={() => {
      return <Button type='primary' style={{marginBottom: 12}} onClick={() => {
        setAct('add')
      }}> 新增</Button> 
    }}></GetAuthList>
    {act === 'add' && <Modal
      onCancel={() => setAct('')}
      visible
      footer={false}
      title={'申请密钥'}
    >
      <Form
      name="basic"
      labelCol={{ span: 4 }}
      wrapperCol={{ span: 16 }}
      initialValues={{ remember: true }}
      onFinish={handleSq}

      autoComplete="off"
      // ref={(e) => { fromRef.current = e }}
    >
      <Form.Item
        label="名称"
        name="title"
      >
        <Select options={key.map((e) => {
          return {
            label: e.title,
            value: e.id
          }
        })} />
      </Form.Item>
      <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
        <Button type="primary" htmlType="submit">
          提交
        </Button>
      </Form.Item>
    </Form>
      </Modal>}
    



  </div>
}