import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Breadcrumb, Button, Form, Input, message, List, Avatar } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import moment from 'moment'
import http from '@/service'

/**
 * Auther lizheyu
 * nav1234表示区域 改掉即可
 * 图片保存在public目录下 名称就是nav1/nav2 比如你区域是大厅 那图片就是大厅.jpeg
 * 
 */

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default () => {
  const [info, setInfo] = useState({})
  const [gonggao, setGonggaoo] = useState('')
  const [gonggaoTitle, setGonggaooTitle] = useState('')
  const [gonggaoList, setGonggaoList] = useState([])
  const fromRef = useRef({})
  const onSubmit = (values) => {
    http({
      url: '/api/gonggao',
      method: 'POST',
      data: {
        detail: gonggao,
        title: gonggaoTitle
      }
    }).then((res) => {
      getGonggaoList()
    })
  };

  const getGonggaoList = () => {
    http({
      url: '/api/gonggaoList',
      method: 'POST'

    }).then((res) => {
      res.value && setGonggaoList(res.value)
    })
  }

  useEffect(() => {
    http({
      url: '/api/getInfo'
    }).then((res) => {
      res.value && setInfo(res.value)
    })

    getGonggaoList()



  }, [])
  return <div>

    <p>公告记录</p>

    <List
      itemLayout="horizontal"
      dataSource={gonggaoList}
      renderItem={item => (
         <List.Item>
          <List.Item.Meta
            avatar={<Avatar src="https://joeschmoe.io/api/v1/1" />}
            title={<a>账号{item.title}</a>}
            description={<div>
              <div>{`${item.detail}`}</div>
              <div>{`发布人:${item.username} 发布时间：${moment(item.time).format('YYYY-MM-DD-HH:mm:ss')}`}</div>
              </div>}
          />
        </List.Item>)}
      
    />
    <Input onChange={(e) => setGonggaooTitle(e.target.value)} value={gonggaoTitle}></Input>
    <Input.TextArea onChange={(e) => setGonggaoo(e.target.value)} value={gonggao}></Input.TextArea>
    <Button onClick={onSubmit}>提交</Button>
  </div>
}