import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Space, Table, Button, Input, message, DatePicker, List, Comment, Avatar, Modal, Form, Row, Col, Select } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import http from '@/service'
import moment from 'moment'

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default (props) => {


  const [info, setInfo] = useState({})
  const [signList, setSignList] = useState([])
  const [act, setAct] = useState('')
  const [currBuyBook, setCurrBuyBook] = useState({})
  const [pj, setPj] = useState('')
  const searchFromRef = useRef({})
  const navigate = useNavigate() 
  const handleBuy = (record) => {
    setAct('pj')
    setCurrBuyBook(record)

  }

  const handleClearSearch = () => {
    // getKeyList()
    searchFromRef.current.resetFields()
  }

  const onSearch = (values) => {
    console.log(values)
    getKeyAuthList({
      ...values,
    })
  }


  const getKeyAuthList = (values = {}) => {
    let data = {}
    data = values.isAgree !== undefined ? {
      isComp: values.isAgree === 'dsh' ? false : true,
      isAgree: values.isAgree !== 'dsh' ? (values.isAgree === true ? true : false) : undefined
    } : {}

    http({
      url: '/api/getKeyAuthList',
      data: data,
      method: 'POST'
    }).then((res) => {
      if (values.title) {
        res.value = res.value.filter((e) => e.key[0].title.includes(values.title))
      }
      setSignList(res.value)
    })

  }

  const handleAgree = (record, flag) => {
    Modal.confirm({
      title: '请确认操作',
      onOk: () => {
        http({
          url: '/api/agreeGetKeyAuth',
          data: { id: record.id, keyId: record.keyId, isAgree: flag, isComp: true },
          method: 'POST'
        }).then((res) => {
          message.success('审核成功')
          getKeyAuthList()
        })
      }
    })

  }

  const handleDel = (r) => {
    http({
      url: '/api/delSign',
      data: { _id: r._id },
      method: 'POST'
    }).then((res) => {
      message.success('删除成功')
      // getSignList()
    })
  }
  const columns = [
    {
      title: '申请用户',
      dataIndex: 'username',
      key: 'username',
    
    },
    {
      title: '审核状态',
      dataIndex: 'isAgree',
      key: 'isAgree',
      render: (v) => {
        return v === true ? '已通过' : v===false ? '已拒绝' : '待审核'
      }

    },
    {
      title: '申请密钥名',
      dataIndex: 'key',
      key: 'key',
      render: (v) => {
        return v[0]?.title
      }


    },

    {
      title: '操作',
      key: 'action',
      render: (text, record) => (
        <Space size="middle">
          {!record.isComp && info.isAdmin && <Button onClick={() => handleAgree(record, true)}>同意</Button>}
          {!record.isComp && info.isAdmin && <Button onClick={() => handleAgree(record, false)}>拒绝</Button>}
        </Space>
      ),
    },
  ];


  useEffect(() => {
    http({
      url: '/api/getInfo'
    }).then((res) => {
      res.value && setInfo(res.value)

    })

    getKeyAuthList()
  }, [])
  return <div>
        {!props.hideSearch && <Form
      name="basic"
      labelCol={{ span: 8 }}
      wrapperCol={{ span: 16 }}
      initialValues={{ remember: true }}
      onFinish={onSearch}
      autoComplete="off"
      ref={(e) => { searchFromRef.current = e }}
    >
            <Row>
        <Col span={12}>
      <Form.Item
        label="密钥名"
        name="title"
      >
        <Input />
      </Form.Item>
      <Form.Item
        label="申请状态"
        name="isAgree"
      >
        <Select options={[
          {
            label: '待审核',
            value: 'dsh'
          },
          {
            label: '已拒绝',
            value: false
          },
          {
            label: '已同意',
            value: true
          },
        ]} />
      </Form.Item>
      </Col>
      </Row>
      <Row>
        <Col span={12}>
      {/* <Form.Item
        label="开始时间"
        name="startTime"
      >
        <DatePicker style={{width: '100%'}} />
      </Form.Item>
      </Col>
      <Col span={12}>
      <Form.Item
        label="结束时间"
        name="endTime"
      >
        <DatePicker style={{width: '100%'}} />
      </Form.Item> */}
      </Col>
      </Row>
      <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
        <Button type="primary" htmlType="submit">
          搜索
        </Button>
        <Button onClick={handleClearSearch} style={{marginLeft: 12}}>
          清空
        </Button>
      </Form.Item>
    </Form>}
    {props.btnRender && props.btnRender()}
        <Table pagination={false} columns={columns} dataSource={signList.filter(props.filter ? props.filter : (e) => info.isAdmin || info.username === e.username)} />


  </div>
}