import React, { useEffect, useRef, useState } from 'react'
import { Layout, Menu, Breadcrumb, Button, Form, Input, message, Space, Table, Upload, Radio, DatePicker, Row, Col } from 'antd';
import { UserOutlined, LaptopOutlined, NotificationOutlined } from '@ant-design/icons';
import http from '@/service'
import moment from 'moment'
import {useNavigate, useSearchParams} from 'react-router-dom'
import SignList from '../signList'
import GetAuthList from '../getAuthList/index'

/**
 * Auther lizheyu
 * nav1234表示区域 改掉即可
 * 图片保存在public目录下 名称就是nav1/nav2 比如你区域是大厅 那图片就是大厅.jpeg
 * 
 */

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;



export default (props) => {
  const [info, setInfo] = useState({})
  const [act, setAct] = useState('')
  const [currKey, setCurrKey] = useState('')
  const [key, setKey] = useState([])
  const fromRef = useRef({})
  const search = useSearchParams()
  const navigate = useNavigate()

  const handleDel = (record) => {
    http({
      url: '/api/delKey',
      method: 'POST',
      data: {
        id: record.id
      }
    }).then((res) => {
      getKeyList()
    })
  }

  const handleEdit = (record) => {
    setAct('edit')
    setCurrKey(record.id)
    setTimeout(() => {
      fromRef.current.setFieldsValue({ ...record })
    }, 10);
  }

  const handleFrozen = (record) => {
    http({
      url: '/api/addKey',
      data: { ...record, isFrozen: !record.isFrozen },
      method: 'POST'
    }).then(() => {
      getKeyList()
    })
  }

  const handleGetAuth = (record) => {
    http({
      url: '/api/getKeyAuth',
      method: 'POST',
      data: {
        keyId: record.id,
      }
    }).then((res) => {
      message.success('申请成功')
    })
  }

  const toDetail = (record) => {
    
  }

  const columns = [
    {
      title: '名称',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: '用户名',
      dataIndex: 'username',
      key: 'username',
    },
    {
      title: 'ID',
      dataIndex: 'id',
      key: 'id',
    },
    {
      title: '密钥类型',
      dataIndex: 'envType',
      key: 'envType',
    },
    {
      title: '状态',
      dataIndex: 'isFrozen',
      key: 'isFrozen',
      render: (v,r) => {
        return r.isCheck ? (v ? '已冻结' : '未冻结') : '待审核'
      }
    },

    {
      title: '创建时间',
      dataIndex: 'time',
      key: 'time',
      render: (v) => {
        return moment(v).format('YYYY-MM-DD HH:mm:ss')
      }
    },

  ];

  const onFinish = (values) => {
    console.log('Success:', values);
    http({
      url: '/api/addKey',
      data: { ...values, id: currKey || undefined },
      method: 'POST'
    }).then((res) => {
      if (res.code === 0) {
        fromRef.current.setFieldsValue({})
        setAct('')
        setCurrKey('')
        getKeyList()
        message.success('操作成功')
      }
    })
  };

  const onSearch = (values) => {
    console.log(values)
    getKeyList({
      ...values,
      startTime: values.startTime?.valueOf(),
      endTime: values.endTime?.valueOf()
    })
  }

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo);
  };

  const getKeyList = (data) => {
    http({
      url: '/api/keyList',
      method: 'POST',
      data
    }).then((res) => {
      res.value && setKey(res.value)

    })
  }

  useEffect(() => {
    http({
      url: '/api/getInfo'
    }).then((res) => {
      res.value && setInfo(res.value)

    })
    getKeyList()
  }, [])
  return <div>
    <h3>基本信息</h3>
    <Table pagination={false} columns={columns} dataSource={key.filter((e) => e.id === search[0].get('keyId'))} />
    <h3>签名记录</h3>
    <SignList filter={(e) => e.keyId === search[0].get('keyId')}></SignList>

    <h3>审核记录</h3>
    <GetAuthList hideSerach filter={(e) => e.keyId === search[0].get('keyId') && (e.username === info.username || info.isAdmin)}></GetAuthList>
    <Button style={{marginTop :12}} onClick={() => {
      navigate('/fabu')
    }}>返回</Button>

  </div>
}